<template>
  <div class="hello">
    <b-container fluid>
      <h1>{{ msg }}</h1>
      <div>Today is  {{ tDate }} </div>
      <h4>My Portfillo</h4>
      <form @submit.prevent="addStock">
      <input type="text" v-model="newStk"  placeholder="Add new Stock Code" name="symbol" v-validate="'min:3'">
      <p v-if="errors.has('symbol')">{{errors.first('symbol')}}</p>
      </form>
      <ul>
        <li v-for= "(s, index) in myStocks" :key="index">
          <!-- <a v-bind:href="/"> -->
          {{ s.symbol }}: {{ s.name  }}
          <!-- </a> -->
          <b-button size="sm" variant="link" v-on:click="removeStock(index)"> remove</b-button>
        </li>
      </ul>
      <div>
          <b-button size="sm" variant="link" v-on:click="getdata()"> getdata(test)</b-button>
          <p>{{ localresult }}</p>
      </div>
    </b-container>
  </div>
</template>

<script>
export default {
  name: 'Home',
  data() {
    return {
      msg: "Welcome to Stock",
      tDate: new Date().toString(),
      newStk: '',
      myStocks: [
        {"symbol" : '600001', "name": "浦发银行"},
        {"symbol" : '600848', "name": "上海临港"},
        ],
      localresult:''
    }
  },
  methods: {
    addStock() {
      this.$validator.validateAll().then( (result) =>{
        if(result){
              this.myStocks.push( {symbol: this.newStk, name: this.newStk})
              this.newStk = ''
        }
      })
    },
    removeStock: function(index) {
      // console.log(index);
      this.myStocks.splice(index, 1)
    },
    getdata: function(){
      // test local call
      const path = 'localhost:5000/api/random'
        this.$http.get(path).then(data => {
          // success callback
          console.log(data.data)
         this.localresult = data.data.rn
        });
    }
  }

}
</script>

