<template>
  <div class="hello">
    <h1>{{ stockId }}</h1>
    <div>Stock Detail  {{ name }}</div>
      <b-button size="sm" variant="link" v-on:click="getStockInfo('600848')">get</b-button>
      <ul>
        <li v-for="(data, idx) in stockData" :key="idx" class="">
          <!-- {{ data }} -->
          <p class="">
            {{data['name']}}, {{ data['price']}}，    成交量： {{ data['volume'] }}
          </p>
        </li>
      </ul>
  </div>
</template>

<script>
export default {
  name: 'StockDetail',
  data() {
    return {
      stockId : this.$route.params.id,
      name: 'stock name',
      stockData:[]
    }
  },
  methods: {
    getStockInfo: function(symbol){
      var symbol = symbol
      this.$http.jsonp('http://api.money.126.net/data/feed/0000001,0601857,0601600,0601398,0600031,1000002,0600848,money.api',{
                // params : {marked:'jjj'}
            }).then(response => {
          // success callback
          console.log(response.data)
          for (var k in response.data) {
                var result = response.data[k]
                this.stockData.push(result)
                
              }
        }, response => {
          // error callback
          console.log('error')
        });
    }
  }
}
</script>

