// router/index.js
import Vue from 'vue'
import router from 'vue-router'
import Home from '@/components/Home'
import Portfolio from '@/components/Portfolio'
import Stock from '@/components/Stock'

Vue.use(router)

export default new router({
    mode: 'history',
    routes: [{
        path: '/',
        name: 'Home',
        component: Home
    }, {
        path: '/Portfolio/',
        name: 'Portfolio',
        component: Portfolio
    }, {
        path: '/StockDetail/:id',
        name: 'Stock',
        component: Stock
    }]
})